import React from "react";
import PropTypes from "prop-types";
// import { Link } from "react-router-dom";

const objectToArraySelector = (obj) => {
  return Object.keys(obj).map((key) => obj[key]);
};

const AncillaryList = ({
  ancillaries,
  onChange,
  onNewItemChange,
  onDeleteClick,
  onUpdateClick,
  onSave,
  addAncillary = false,
  onClose,
  saving = false,
  deleting = null,
  updating = null,
}) => {
  const ancillaryArray = objectToArraySelector(ancillaries.value);
  return (
    <table className="table">
      <thead>
        <tr>
          <th>Name</th>
          <th />
          <th />
        </tr>
      </thead>
      <tbody>
        {ancillaryArray
          .filter((a) => a)
          .map((ancillary) => (
            <tr key={ancillary.id}>
              <td>
                <input
                  type="text"
                  name="addAncillary"
                  className="form-control"
                  value={ancillary.value}
                  placeholder="Enter value"
                  onChange={() => onChange(event, ancillary.id)}
                />
              </td>
              <td>
                <button
                  className="btn btn-outline-primary"
                  disabled={addAncillary}
                  onClick={() => onUpdateClick(ancillary.id)}
                >
                  {updating && updating === ancillary.id
                    ? "updating..."
                    : "update"}
                </button>
              </td>
              <td>
                <button
                  className="btn btn-outline-danger"
                  onClick={() => onDeleteClick(ancillary)}
                >
                  {deleting && deleting === ancillary.id
                    ? "Deleting.."
                    : "Delete"}
                </button>
              </td>
            </tr>
          ))}
        {addAncillary && (
          <tr>
            <td>
              <input
                type="text"
                name="addAncillary"
                className="form-control"
                placeholder="add ancillary.."
                onChange={() =>
                  onNewItemChange(event, ancillaryArray.length + 1)
                }
              />
            </td>
            <td>
              <button className="btn btn-primary" onClick={onSave}>
                {saving ? "saving.." : "save"}
              </button>
              <button className="close" aria-label="Close" onClick={onClose}>
                <span aria-hidden="true">&times;</span>
              </button>
            </td>
          </tr>
        )}
      </tbody>
    </table>
  );
};

AncillaryList.propTypes = {
  ancillaries: PropTypes.object.isRequired,
  onDeleteClick: PropTypes.func.isRequired,
  addAncillary: PropTypes.bool,
  onChange: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
  saving: PropTypes.bool,
  deleting: PropTypes.number,
  updating: PropTypes.number,
  onNewItemChange: PropTypes.func.isRequired,
  onUpdateClick: PropTypes.func.isRequired,
};

export default AncillaryList;
