import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import * as flightActions from "../../redux/actions/flightActions";
import * as ancillaryActions from "../../redux/actions/ancillaryActions";
import { newFlight, newAncillaries } from "../../../tools/mockData";
import AncillaryList from "./AncillaryList";
import Spinner from "../common/Spinner";
import { toast } from "react-toastify";

class AncillariesPage extends React.Component {
  state = {
    addAncillary: false,
    updating: null,
    ancillaries: newAncillaries,
    ancillary: { id: null, value: "" },
    saving: false,
    deleting: null,
  };
  componentDidMount() {
    const {
      loadFlights,
      flights,
      // ancillaries,
      loadAncillaries,
      flightAncillaries,
    } = this.props;
    if (flights.length === 0) {
      loadFlights().catch((error) => alert("Loading flights failed " + error));
    }
    if (!flightAncillaries.id) {
      console.log("ancillaries not present");
      loadAncillaries().catch((error) =>
        alert("Loading ancillaries failed " + error)
      );
    } else {
      this.setState({ ancillaries: flightAncillaries });
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.flightAncillaries !== this.props.flightAncillaries) {
      this.setState({ ancillaries: this.props.flightAncillaries });
    }
  }

  handleClick = () => {
    console.log("handle click");
    this.setState({ addAncillary: true });
  };

  handleChange = (event, ancillaryId) => {
    this.setState({
      ancillary: { id: ancillaryId, value: event.target.value },
    });
    this.setState((prevState) => {
      return {
        ancillaries: {
          ...prevState.ancillaries,
          value: {
            ...prevState.ancillaries.value,
            [ancillaryId]: {
              ...prevState.ancillaries.value[ancillaryId],
              value: event.target.value,
            },
          },
        },
      };
    });
  };

  handleNewItemChange = (event, ancillaryId) => {
    this.setState({
      ancillary: { id: ancillaryId, value: event.target.value },
    });
  };

  handleUpdate = (ancillaryId) => {
    this.setState({ updating: ancillaryId });
    this.props
      .saveAncillary(this.state.ancillaries)
      .then(() => {
        this.setState({ updating: null });
        toast.success("Item updated");
      })
      .catch((error) => {
        this.setState({ updating: null });
        toast.error("updating item failed");
        console.log("updating ancillary failed " + error);
      });
  };

  handleDelete = (ancillary) => {
    this.setState({ deleting: ancillary.id });
    // const { value: value, ...withOutValue } = this.props.flightAncillaries;
    // const { [ancillary.id]: removedValue, ...withOutRemoved } = value;
    // console.log("removed value: ", removedValue);
    // const res = { ...withOutValue, value: withOutRemoved };
    let tempAncillaries = {
      ...this.props.flightAncillaries,
      value: {
        ...this.props.flightAncillaries.value,
        [ancillary.id]: null,
      },
    };
    this.props
      .saveAncillary(tempAncillaries)
      .then(() => {
        this.setState({ deleting: null });
        toast.success("Item deleted");
      })
      .catch((error) => {
        this.setState({ deleting: null });
        toast.error("Entry deletion failed");
        console.log("delete ancillary failed " + error);
      });
  };

  ancillaryIsValid = () => {
    const { value } = this.state.ancillary;
    return value;
  };
  handleSave = () => {
    if (!this.ancillaryIsValid()) {
      this.setState({ addAncillary: false });
      return;
    }
    this.setState({ saving: true });
    let tempAncillaries = {
      ...this.props.flightAncillaries,
      value: {
        ...this.props.flightAncillaries.value,
        [this.state.ancillary.id]: {
          id: this.state.ancillary.id,
          value: this.state.ancillary.value,
        },
      },
    };
    this.props
      .saveAncillary(tempAncillaries)
      .then(() => {
        this.setState({ saving: false });
        this.setState({ addAncillary: false });
        toast.success("Item added");
      })
      .catch((error) => {
        this.setState({ saving: false });
        toast.error("adding ancillary failed");
        console.log("adding ancillary failed " + error);
      });
  };

  handleClose = () => {
    this.setState({ addAncillary: false });
  };
  render() {
    return (
      <>
        <h2>Ancillary Services</h2>
        {this.props.flights.length === 0 ||
        this.props.ancillaries.length === 0 ? (
          <Spinner />
        ) : (
          <>
            <button
              style={{ marginTop: 10, marginBottom: 20, float: "left" }}
              className="btn btn-primary"
              onClick={this.handleClick}
            >
              Add Ancillary
            </button>
            <AncillaryList
              ancillaries={this.state.ancillaries}
              onDeleteClick={this.handleDelete}
              onUpdateClick={this.handleUpdate}
              updateItemId={this.state.updateItemId}
              addAncillary={this.state.addAncillary}
              onChange={this.handleChange}
              onSave={this.handleSave}
              onClose={this.handleClose}
              saving={this.state.saving}
              deleting={this.state.deleting}
              updating={this.state.updating}
              onNewItemChange={this.handleNewItemChange}
            ></AncillaryList>
          </>
        )}
      </>
    );
  }
}

AncillariesPage.propTypes = {
  flights: PropTypes.array.isRequired,
  ancillaries: PropTypes.array.isRequired,
  flightAncillaries: PropTypes.object.isRequired,
  // passengers: PropTypes.array.isRequired,
  loadFlights: PropTypes.func.isRequired,
  loadAncillaries: PropTypes.func.isRequired,
  saveAncillary: PropTypes.func.isRequired,
  // loadPassengers: PropTypes.func.isRequired,
};

function getFlightByName(flights, name) {
  return flights.find((a) => a.name === name) || newFlight;
}

function mapStateToProps(state, ownProps) {
  const flightName = ownProps.match.params.name;
  const flight =
    state.flights.length > 0
      ? getFlightByName(state.flights, flightName)
      : newFlight;
  const flightAncillaries =
    state.ancillaries.length > 0 && flight.id
      ? state.ancillaries.find((a) => a.id === flight.ancillariesId)
      : newAncillaries;
  return {
    flightAncillaries,
    flights: state.flights,
    ancillaries: state.ancillaries,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    loadFlights: bindActionCreators(flightActions.loadFlights, dispatch),
    loadAncillaries: bindActionCreators(
      ancillaryActions.loadAncillaries,
      dispatch
    ),
    saveAncillary: bindActionCreators(ancillaryActions.saveAncillary, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(AncillariesPage);
